body {
  padding: 1.45em 14em;
  line-height: 1.45em;
  width: 40em;
  margin: 0 auto; }

header {
  text-align: center; }
  header h1 a {
    border-bottom: none; }
  header nav ul {
    padding: 0;
    margin: 1.45em 0; }
    header nav ul li {
      display: inline; }
    header nav ul li:before {
      content: "\2022 \0020";
      font-size: .8em; }
    header nav ul li:first-child:before {
      content: ""; }

#sidebar {
  position: absolute;
  margin-left: -14em;
  width: 10em; }
  #sidebar .twtr-widget a {
    text-decoration: none !important; }
  #sidebar a {
    border-bottom: none; }
    #sidebar a:hover {
      border-bottom: 1px solid; }
  #sidebar .parent {
    margin: 0;
    padding: 0; }
    #sidebar .parent > li {
      margin: 0 0 1.45em;
      padding: 0; }
      #sidebar .parent > li.active a {
        border-bottom: 1px solid; }
      #sidebar .parent > li.active li a {
        border-bottom: none; }

.children {
  display: none;
  margin: 0;
  padding: 0; }
  .children li {
    margin-bottom: 0;
    margin-left: 1.3em;
    text-indent: -1.1em; }
    .children li:before {
      content: "\2014 \0020"; }

.active .children {
  display: block; }

.main-content {
  margin-top: 1.45em; }

.media ul {
  list-style: none;
  padding: 0;
  margin: 1.45em 0 0; }
  .media ul.pdf {
    margin-top: 1.45em; }

.intro-image {
  margin: 1.45em 0; }

[role="main"] img {
  max-width: 100%; }

#about .image {
  float: left;
  margin-left: 1em; }

#raphael-score {
  position: absolute;
  left: 0;
  width: 100%; }
  #raphael-score:before {
    position: fixed;
    content: "";
    width: 100%;
    border-bottom: 1px solid red; }

@media print {
  * {
    background: transparent !important;
    color: black !important;
    box-shadow: none !important;
    text-shadow: none !important; }
  /* Black prints faster: h5bp.com/s */
  a, a:visited {
    text-decoration: underline; }
  a[href]:after {
    content: " (" attr(href) ")"; }
  abbr[title]:after {
    content: " (" attr(title) ")"; }
  .ir a:after, a[href^="javascript:"]:after, a[href^="#"]:after {
    content: ""; }
  /* Don't show links for images, or javascript/internal links */
  pre, blockquote {
    border: 1px solid #999;
    page-break-inside: avoid; }
  thead {
    display: table-header-group; }
  /* h5bp.com/t */
  tr, img {
    page-break-inside: avoid; }
  img {
    max-width: 100% !important; }
  @page {
    margin: 0.5cm; }

  p, h2, h3 {
    orphans: 3;
    widows: 3; }
  h2, h3 {
    page-break-after: avoid; } }

em, strong, h1, h2, h3 {
  font-weight: normal; }

#sidebar .parent > li > a, h1, h2, h3 {
  font-weight: normal;
  font-size: 0.75em;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  word-spacing: 0.2em; }

body, input, textarea {
  font-family: 'Transport W01', sans-serif;
  line-height: 1.45em;
  font-size: 100%;
  word-spacing: -0.03em; }

h1, h2, h3 {
  margin: 1.45em 0 0; }

p {
  margin: 0; }

p + p {
  text-indent: 2em; }

a {
  color: #000;
  text-decoration: none;
  border-bottom: 1px solid; }

a.image-link {
  border-bottom: none; }

hr {
  border: none;
  margin: 1.45em 0; }
  hr:after {
    content: "***";
    text-align: center;
    width: 100%;
    display: block; }

article {
  margin-bottom: 1.45em; }


